.registration__input {
    width: 100%;
    padding-bottom: 13px;
    margin-bottom: 30px;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: #ccc;
    border: none;
    border-bottom: 2px solid #ccc;
    background-color: black;
    box-sizing: border-box;
  }

  .registration__input:focus {
    outline: none;
  }